import './Header.css';
import { Link } from "react-router-dom";
const Header=()=>{
    return(<header>
        <nav className="flex row items-center space-btw">
        <h2>
        <Link to={'/Home'}>Raj Screen Recorder</Link>
        </h2>
            <ul className="flex row text-center">
                <Link to={'/Home'}><li className="nav-btn anim-btn">Home</li></Link>
                <Link to={'/Record'}><li className="nav-btn anim-btn">Record</li></Link>
            </ul>   
        </nav>
    </header>);
}
export default Header;