import "./Recorder.css";
// import useUnload from "./unload";
import { useReactMediaRecorder } from "react-media-recorder";

const Recorder = () => {
  // useUnload(e => {
  //   stopRecording();
  // });
  const { status, startRecording, stopRecording, mediaBlobUrl } =
    useReactMediaRecorder({ screen: true, video:true,audio:false });

  return (
    <div className="text-center flex col recorder">
      <h1>
        {status === "idle" && <p>Recording is not Started</p>}
        {status === "acquiring_media" && <p>Please provide access to Screen</p>}
        {status === "recording" && <p>Recording is now Started</p>}
        {status === "stopped" && <p>Recording is Stopped</p>}
      </h1>
      <div className="justify-center flex cta-btns">
        <button className="btn anim-btn" onClick={startRecording} disabled={status === "recording"}>
          Start Recording
        </button>
        <button className="btn anim-btn" onClick={stopRecording} disabled={status === "idle" || status === "stopped"} >
          Stop Recording
        </button>
      </div>
      {mediaBlobUrl && (
        <div className="justify-center flex col video-player-div">
          <video className="video-player" src={mediaBlobUrl} controls autoPlay loop />
          <a className="btn anim-btn" href={mediaBlobUrl} download>
            Save Video
          </a>
        </div>
      )}
    </div>
  );
};
export default Recorder;
