import './Footer.css';
import { Link } from "react-router-dom";

const Year = () =>{
    let date = new Date();
    return date.getFullYear();
}
const Footer=()=>{
    return(<footer className='flex row space-btw'>
        <span>Raj Screen Recorder</span>
        <span><a href="https://varshithrajbasa.github.io/">About</a></span>
        <span>&#169; {Year()}</span>
    </footer>);
}
export default Footer;